import styled from "styled-components";
import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

import Format from "../../assets/images/Formats.webp";
import Logo_img_1 from "../../assets/images/intro_Img_1.webp";
import Logo_img_2 from "../../assets/images/intro_Img_2.webp";
import Logo_img_3 from "../../assets/images/intro_img_3.webp";
import Logo_img_4 from "../../assets/images/intro_img_4.webp";

import { useEffect, useRef, useState } from "react";

const ScrollWrapper = styled.div`
  height: ${({ height }) => height + 1000}px;
  width: 100vw;
  display: flex;
`;

const SFormats = styled.section`
  background: radial-gradient(
      56.92% 42.11% at 111.57% 109.48%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      71.73% 87.75% at -17.08% -20.39%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;

  padding: 70px 0;
  border-radius: 30px;
  /* top: 110px; */

  left: 0;
  width: 100vw;
  height: fit-content;
  z-index: 100;

  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;

    & > p {
      text-align: center;
      margin-bottom: 43px;
    }
  }
`;

const Title = styled(Title2)`
  margin-bottom: 40px;
  font-size: 26px;
`;

const FormatList = styled.div`
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 760px;
  align-self: flex-start;
  transition: 0.1s linear;
  gap: 27px;
  min-height: 235px;
  height: 58vw;
  overflow: hidden;
  padding: 10px;
  gap: 50px;
  margin-bottom: 15px;
`;

const FrameBox = styled.div`
  opacity: 0;
  position: absolute;
  height: fit-content;

  &:nth-child(even) > div:last-child {
    transform: translate(-40%, 50%) rotate(-3.4deg);
    border-top-right-radius: 0;

    svg {
      right: 0;
    }
  }

  &:nth-child(odd) > div:last-child {
    border-top-left-radius: 0;
    transform: translate(-40%, 50%) rotate(6.9deg);

    svg {
      left: 0;
    }
  }
  
  transition: 0.5s ease-in-out;
  ${({ active = false }) => active && `opacity: 1;`}
`;

const Frame = styled.div`
  top: 10px;
  left: 10px;
  margin: 0 auto;
  overflow: hidden;
  outline: 9px solid rgba(255, 255, 255, 0.1);
  border-radius: 30px;

  width: 85vw;
  min-width: 250px;
  min-height: 200px;
  height: fit-content;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  img {
    width: 100%;
    margin-bottom: -5px;
  }
`;

const Tag = styled.div`
  position: absolute;
  bottom: 0;
  left: 50%;
  padding: 10px 15px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 10px;
  line-height: 120%;
  letter-spacing: 0.01em;
  white-space: nowrap;
  color: #ffffff;

  background: radial-gradient(
      156.41% 159.7% at -13.81% 12.62%,
      #d83575 3.13%,
      #7e03f4 59.16%,
      #1010f2 100%
    ),
    #4354f1;
  box-shadow: inset 0px 0px 25.3184px rgba(255, 255, 255, 0.25);

  svg {
    position: absolute;
    top: 0;
    transform: translateY(-99%);
  }
`;

const Button = styled.button`
  border: 0;
  cursor: pointer;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;
  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  padding: 20px 60px;
  transition: 0.25s ease-in-out;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }
`;

const PointBox = styled.div`
  display: flex;
  margin-bottom: 70px;
  gap: 16px;
`;

const Point = styled.div`
  width: 10px;
  height: 10px;
  border-radius: 12px;
  transition: 0.5s ease-in-out;
  background-color: #ffffff66;
  transform: scale(1);

  ${({ active = false }) =>
    active &&
    `
      transform: scale(1.1);
      background: #FFFFFF;
      box-shadow: 0px 0px 11px #E0DEEA;
    `}
`;

const Formats = ({ language }) => {
  const list = useRef(null);
  const formats = useRef(null);

  const [activeFormat, setActiveFormat] = useState(0);

  useEffect(() => {
    const Slide = setInterval(() => {
      if (activeFormat < 6) {
        setActiveFormat(activeFormat + 1);
      } else {
        setActiveFormat(0);
      }
    }, 3000);

    return () => clearInterval(Slide);
  }, [activeFormat]);

  return (
    <ScrollWrapper>
      <SFormats ref={formats}>
        <InnerWrap>
          <Title>Доступные форматы</Title>

          <FormatList ref={list} className="sticky-container">
            <FrameBox active={0 === activeFormat}>
              <Frame>
                <img src={Format} alt="Format_1" loading="lazy" />
              </Frame>

              <Tag>
                AR анимация <br /> вокруг стримера
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M0.0112324 0V23H13.0112H30.0112C16.0112 23 -0.48877 10.999 0.0112324 0Z"
                    fill="url(#paint0_linear_412_2)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_2"
                      x1="29.0112"
                      y1="22.999"
                      x2="0.51123"
                      y2="22.999"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#CB429E" />
                      <stop offset="1" stop-color="#D54E96" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>

            <FrameBox active={1 === activeFormat}>
              <Frame>
                <img src={Logo_img_1} alt="Format_2" loading="lazy" />
              </Frame>

              <Tag>
                Появляющийся баннер <br /> в нужном месте
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M30 0V23H17H1.90735e-06C14 23 30.5 10.999 30 0Z"
                    fill="url(#paint0_linear_412_130)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_130"
                      x1="30"
                      y1="23"
                      x2="-4.03453e-07"
                      y2="23"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#7534F6" />
                      <stop offset="1" stop-color="#7425F5" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>

            <FrameBox active={2 === activeFormat}>
              <Frame>
                <img src={Format} alt="Format_3" loading="lazy" />
              </Frame>

              <Tag>
                AR анимация <br /> вокруг стримера
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M0.0112324 0V23H13.0112H30.0112C16.0112 23 -0.48877 10.999 0.0112324 0Z"
                    fill="url(#paint0_linear_412_2)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_2"
                      x1="29.0112"
                      y1="22.999"
                      x2="0.51123"
                      y2="22.999"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#CB429E" />
                      <stop offset="1" stop-color="#D54E96" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>

            <FrameBox active={3 === activeFormat}>
              <Frame>
                <img src={Logo_img_2} alt="Format_1" loading="lazy" />
              </Frame>

              <Tag>
                Появляющийся баннер <br /> в нужном месте
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M30 0V23H17H1.90735e-06C14 23 30.5 10.999 30 0Z"
                    fill="url(#paint0_linear_412_130)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_130"
                      x1="30"
                      y1="23"
                      x2="-4.03453e-07"
                      y2="23"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#7534F6" />
                      <stop offset="1" stop-color="#7425F5" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>

            <FrameBox active={4 === activeFormat}>
              <Frame>
                <img src={Format} alt="Format_2" loading="lazy" />
              </Frame>

              <Tag>
                AR анимация <br /> вокруг стримера
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M0.0112324 0V23H13.0112H30.0112C16.0112 23 -0.48877 10.999 0.0112324 0Z"
                    fill="url(#paint0_linear_412_2)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_2"
                      x1="29.0112"
                      y1="22.999"
                      x2="0.51123"
                      y2="22.999"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#CB429E" />
                      <stop offset="1" stop-color="#D54E96" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>

            <FrameBox active={5 === activeFormat}>
              <Frame>
                <img src={Logo_img_3} alt="Format_3" loading="lazy" />
              </Frame>

              <Tag>
                Появляющийся баннер <br /> в нужном месте
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M30 0V23H17H1.90735e-06C14 23 30.5 10.999 30 0Z"
                    fill="url(#paint0_linear_412_130)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_130"
                      x1="30"
                      y1="23"
                      x2="-4.03453e-07"
                      y2="23"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#7534F6" />
                      <stop offset="1" stop-color="#7425F5" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>

            <FrameBox active={6 === activeFormat}>
              <Frame>
                <img src={Logo_img_4} alt="Format_3" loading="lazy" />
              </Frame>

              <Tag>
                AR анимация <br /> вокруг стримера
                <svg
                  width="31"
                  height="23"
                  viewBox="0 0 31 23"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M0.0112324 0V23H13.0112H30.0112C16.0112 23 -0.48877 10.999 0.0112324 0Z"
                    fill="url(#paint0_linear_412_2)"
                  />
                  <defs>
                    <linearGradient
                      id="paint0_linear_412_2"
                      x1="29.0112"
                      y1="22.999"
                      x2="0.51123"
                      y2="22.999"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stop-color="#CB429E" />
                      <stop offset="1" stop-color="#D54E96" />
                    </linearGradient>
                  </defs>
                </svg>
              </Tag>
            </FrameBox>
          </FormatList>

          <PointBox>
            <Point
              onClick={() => setActiveFormat(0)}
              key={0}
              active={0 === activeFormat}
            ></Point>
            <Point
              onClick={() => setActiveFormat(1)}
              key={1}
              active={1 === activeFormat}
            ></Point>
            <Point
              onClick={() => setActiveFormat(2)}
              key={2}
              active={2 === activeFormat}
            ></Point>
            <Point
              onClick={() => setActiveFormat(3)}
              key={3}
              active={3 === activeFormat}
            ></Point>
            <Point
              onClick={() => setActiveFormat(4)}
              key={4}
              active={4 === activeFormat}
            ></Point>
            <Point
              onClick={() => setActiveFormat(5)}
              key={5}
              active={5 === activeFormat}
            ></Point>
            <Point
              onClick={() => setActiveFormat(6)}
              key={6}
              active={6 === activeFormat}
            ></Point>
          </PointBox>

          <Text size="26">
            Узнайте, какой формат подойдет <br /> именно для вашего продукта:
          </Text>
          <Button>Начать общение</Button>
        </InnerWrap>
      </SFormats>
    </ScrollWrapper>
  );
};

export default Formats;
